* {
  letter-spacing: .5px;
  box-sizing: border-box;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
}

.viewport {
  background-color: #000;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.ui {
  position: fixed;
  top: 5px;
  left: 5px;
}

.menus {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.menu {
  color: #fff;
  background: #ffffff4d;
  border-radius: 4px;
  width: 200px;
  padding: 4px 6px;
}

.menu:not(.active) > :not(.title) {
  display: none;
}

.menu.active > .title {
  margin-bottom: 4px;
}

.menu:has(input[type="text"]:last-of-type) {
  padding-bottom: 6px;
}

.title {
  font-weight: bold;
}

.options {
  padding-left: 8px;
}

input[type="radio"] {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

input[type="radio"] + label {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  padding-left: 20px;
  display: block;
  position: relative;
}

input[type="radio"] + label:before {
  content: "";
  aspect-ratio: 1;
  background: gray;
  border-radius: 2px;
  height: calc(100% - 6px);
  position: absolute;
  top: 3px;
  left: 3px;
}

input[type="radio"]:checked + label:before {
  background: #1e90ff;
}

input[type="text"] {
  color: #fff;
  background: #0000004d;
  border: none;
  border-radius: 2px;
  width: 100%;
  padding: 2px 6px;
}

